import {
	init,
	renderChat,
	// renderVideo,
	setChannelUrl,
} from '@vkplaylive/sdk';
import '@vkplaylive/sdk/style.css';

init({
	xSdkApp: 'test_app_qwerty',
});

setChannelUrl('foobarbuz-1');

renderChat('#vkplaylive-chat');
// renderVideo('#vkplaylive-video');

document.querySelector('#button-stream-url').addEventListener('click', () => {
	const value = document.querySelector('#input-stream-url').value;

	setChannelUrl(value);
});
